<template>
  <div class="index-wrap">
    <div class="index-left">
      <div class="index-left-block">
        <h2></h2>
        <template v-for="product in productList">
          <h3 :key="product.id">{{ product.title }}</h3>
          <ul :key="product.id">
            <li v-for="item in product.list">
              <a :href="item.url">{{item.name}}</a>
              <span v-if="item.hot" class="hot-tag">HOT</span>
            </li>
          </ul>
        </template>
      </div>
      <div class="index-left-block lastest-news">
        <h2></h2>
        <ul>
          <li v-for="item in newsList">
            <a :href="item.url" class="new-item">{{ item.title }}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <!-- slideShow  html  - vue2.0  -->
      <slide-show :slides="slides" :inv="invTime" @onchange="doSomethindOnSlideChanged"></slide-show>
      <div class="index-board-list">
        <div
          class="index-board-item"
          v-for="(item, index) in boardList"
          :class="[{'line-last' : index%2!==0}, 'index-board-'+item.id]"
          :key="item.id"
        >
          <div class="index-board-item-inner">
            <h2>{{ item.title }}</h2>
            <p>{{ item.description }}</p>
            <div class="index-board-button">
              <router-link class="button" :to="{path: 'detail/' + item.toKey}"></router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import slideShow from "../components/slideShow";
export default {
  components: {
    slideShow
  },
  created: function() {
    this.$http.get("api/getNewsList").then(
      res => {
        this.newsList = res.data;
      },
      err => {
        console.log(err);
      }
    );
  },
  data() {
    return {
      invTime: 2000,
      slides: [
        {
          // webpack js require
          // jssourcerequire
          src: require("../assets/slideShow/pic1.jpg"),
          title: "xxx1",
          href: "detail/analysis"
        },
        {
          src: require("../assets/slideShow/pic2.jpg"),
          title: "xxx2",
          href: "detail/count"
        },
        {
          src: require("../assets/slideShow/pic3.jpg"),
          title: "xxx3",
          href: "http://xxx.xxx.com"
        },
        {
          src: require("../assets/slideShow/pic4.jpg"),
          title: "xxx4",
          href: "detail/forecast"
        }
      ],
      boardList: [
        {
          title: "",
          description: "",
          id: "car",
          toKey: "analysis",
          saleout: false
        },
        {
          title: "",
          description: "",
          id: "earth",
          toKey: "count",
          saleout: false
        },
        {
          title: "",
          description: "",
          id: "loud",
          toKey: "forecast",
          saleout: true
        },
        {
          title: "",
          description: "",
          id: "hill",
          toKey: "publish",
          saleout: false
        }
      ],
      newsList: [
        {
          title: "baidu",
          url: "http://www.baidu.com"
        },
        {
          title: "sina",
          url: "http://www.sina.com"
        }
      ],
      productList: {
        pc: {
          title: "PC",
          list: [
            {
              name: "",
              url: "http://starcraft.com"
            },
            {
              name: "",
              url: "http://warcraft.com"
            },
            {
              name: "",
              url: "http://overwatch.com",
              hot: true
            },
            {
              name: "",
              url: "http://hearstone.com"
            }
          ]
        },
        app: {
          title: "",
          last: true,
          list: [
            {
              name: "91",
              url: "http://weixin.com"
            },
            {
              name: "",
              url: "http://twitter.com",
              hot: true
            },
            {
              name: "",
              url: "http://maps.com"
            },
            {
              name: "",
              url: "http://phone.com"
            }
          ]
        }
      }
    };
  },
  methods: {
    doSomethindOnSlideChanged: function(index) {
      // console.log("doSomethindOnSlideChanged " + index);
    }
  }
};
</script>

<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px; /** 15 */
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}
.index-left-block .hr {
  margin-bottom: 20px;
}
.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  /**
  float
  
  float
  
  float
  blockfloatinlinefloat*/
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner {
  background: url(../assets/images/1.png) no-repeat;
}
.index-board-loud .index-board-item-inner {
  background: url(../assets/images/2.png) no-repeat;
}
.index-board-earth .index-board-item-inner {
  background: url(../assets/images/3.png) no-repeat;
}
.index-board-hill .index-board-item-inner {
  background: url(../assets/images/4.png) no-repeat;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis; /*  clip ellipsis*/
  white-space: nowrap; /*  */
}
</style>



